a {
    text-decoration: none;
    color: black;
    font-weight: 500;
    font-size: 24px;
    display: block;
    padding-top: 10px;
    width: auto;
}

.footer {
    width: 100%;    
    margin: auto;
    background-color: #ffffff;
    padding: 20px 30px 20px;
    bottom: 0;
    box-sizing: border-box;
    border-top: 2px solid #A5DD9B;
}



.left {
    width: 50%;
    display: inline-block;
    text-align: left;
    padding-left: 30px;
}
.right {
    width: 50%;
    display: inline-block;
    text-align: right;
    padding-right: 30px;
    font-size: 18px;
}

.footer >h4 {
    text-align: center;
    padding: 0;
    padding-top: 20px;
}

.termsOfService{
    font-size: 14px;
    font-weight: 100;
}
.email{
    font-size: 14px;
    font-weight: 100;

}
.contacts{
    font-size: 14px;
    font-weight: 100;
}
.rightsReserved{
    font-size: 12px;
    font-weight: 100;
}
.left > a > img {
    width: 100px    ;
}
#languageSelect {
    padding: 0px 5px;
    border: none;
}