
.faq-container {
    max-width: 100%;
    margin: 0 auto;
    padding: 20px;
    background-color: none;
}

.faq-title {
    margin:20px auto ;
    text-align: center;
    padding-top: 20px;
    padding-bottom: 20px;
}

.faq-item {

    margin-bottom: 20px;
    margin: auto;
    max-width: 600px;
}

.question {
    font-weight: 100;
    cursor: pointer;
    padding: 10px;
    background-color: none;
    border-radius: 5px;
    margin-bottom: 10px;
    font-size: 16px;
}

.question.active {
    background-color: none;
}

.answer {
    display: none;
    padding: 10px;
    background-color: white ;
    border: 1px solid black;
    border-radius: 5px;
    margin-bottom: 10px;
    font-size: 14px;
}

    .answer.active {
        display: block;
    }

.faq-item > .question {
    background: none;

}
