/* ContactsPage.css */

.contacts-container {
    padding: 20px;
    background-color: #f3f3f3;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  

  
  .contacts-info {
    width: 60%;
    margin: auto;
    height: 450px;
  }
  
  .contacts-info p {
    margin: 10px 0;
    font-size: 16px;
  }
  .border {
    background-color: #ffffff;
    border: 1px solid black;
    box-shadow: 1px 1px 1px 1px 10px black;
    border-radius: 5px;
    padding: 30px;
  }
.border > h4 {
  padding-bottom: 10px;
}
  table{
    width: 100%;

  }
  tr {
    padding-top: 10px;
  }
  td{
    padding-left: 20px;
  }