.dnavbar {
    background-color:  #7ed6df;
    padding: 10px 20px;
    display: flex;
    justify-content: space-between;
    /* color: #274c77; */
}

.dnavbar-nav {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    background: none;
}

.dnav-item {
    margin-right: 10px;
}

.dnav-link {
    /* color: #274c77; */
    font-size: 16px;
    text-decoration: none;
    padding: 10px;
}

    .dnav-link:hover {
        border-radius: 5px;
        /* color: #ffc300; */
        background-color: #c7ecee   ;
    }
