.TermsOfServicesC {
    background-color: #f3f3f3;    
    padding: 20px 0 40px;
}
.TermsOfServicesC > div{
    width: 60%;
    margin:auto;
    padding: 10px;
}
.TermsOfServicesC > h1 , h2 {
    font-size: 14px;
    font-weight: 100;
    padding: 15px;
}
.TermsOfServicesC >  h1 {
    font-size: 20px;
}

.TermsOfServicesC > ul {
    padding: 20px;
    width: 300px;
    font-size: 12px;
    margin: auto;
}
.TermsOfServicesC > li {
    font-size: 14px;
}
.TermsOfServicesC > p {
    font-size: 12px;
    padding: 10px;
}   

.TermsOfServicesC >strong{
    font-weight: 700;
}
