.navbar {
    /* background-color:  #C5EBAA; */
    padding: 10px 20px;
    display: flex;
    justify-content: space-between;
    padding: 0 10%;
    /* border-bottom: 2px solid #A5DD9B; */

}

.navbar-nav {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    background: none;
    padding-left: 30px;
}

.nav-item {
    margin-right: 10px;
}

.nav-link {
    text-decoration: none;
    padding: 10px;
    font-size: 14px;
    font-weight: 100;

}
.nav-linkLogo{
    text-decoration: none;
    padding: 10px;
    font-size: 14px;
    font-weight: 100;
}

    .nav-link:hover {
        /* border-radius: 5px; */
        /* background-color: white   ; */
        border-bottom: 2px solid #A5DD9B;
    }

.auth-links {
    display: flex;
    text-decoration: none;
    padding: 10px;
    font-size: 14px;
    font-weight: 100;
}

.nav-item-centered {
    margin: auto;
}


.logo {
    width: 100px;
    padding: 12px    0;

}
.logo > img {
    width: 100%;
}
