.price {
  width: 100%;
  padding: 40px;  
}
.container {
  margin: 0 auto;
  text-align: center;
  white-space: nowrap;
}

.card {
  display: inline-block;
  position: relative;
  color: white;
  width: 200px;
  height: 300px;
  border-radius: 20px;
  overflow: hidden;
  margin: 0 auto;
  text-align: center;
  box-shadow: 0 11px 26px 1px gray /*, inset 0 -16px 90px #c4453640*/;
  padding: 10px;
}


.card > h1 {
  padding-top: 90px;
}

.card-1, .card-3 {
  position: relative;
  transform: scale(0.9);
  background-color: #E1F0DA;
}
.card-3 > h1 {
  color: black;
  font-size: 48px;

}
.card-1 > h1 {
  color: black;
  font-size: 48px;
}
.card-2 > h1 {
  font-size: 56px;
  color: white; 
}
.card-2 > h4 , .card-2 > span {
  color: white;
}
.card-1 {
  left: 10px;
  margin-left: -60px;
  z-index: 0;
}

.card-2 {
  z-index: 1;
  background-color: #A5DD9B;
}
.card-3 {
  left: -10px;
  margin-right: -60px;
  z-index: 0;
}

.pl {
  font-size: 32px;
}
