
.form-container {
    background-color: none;
    padding: 5px;
    border-radius: 8px;
    border: 4px solid #001d3d;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    width: 300px;
    margin: 100px auto;
}

.form-container h2 {
    text-align: center;
    margin-bottom: 20px;
}

.form-group {
    margin-bottom: 20px;
}

.form-group label {
    display: block;
    margin-bottom: 5px;
}

.form-group input {
    width: 100%;
    padding: 10px;
    background-color: none;
    border: 2px solid #001d3d;
    border-radius: 5px;
}

.form-group button {
    width: 100%;
    padding: 10px;
    background-color: #007bff;
    color: #001d3d;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.form-group button:hover {
    background-color: #0056b3;
}

.form-group .forgot-password {
    text-align: right;
    margin-bottom: 10px;
}

.form-group .register-link {
    text-align: center;
}


.forgot-password-container {
    margin: 150px auto 150px;
}