.services-container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.services-list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  width: 80%;
  margin: auto;
}

.services-list li {
  width: 50%;
  box-sizing: border-box;
  padding: 0 10px;
  padding-top: 10px;
}

.services-list li:nth-child(odd) {
  padding-right: 0;
}

.services-list li:nth-child(even) {
  padding-left: 0;
}


.services-container {
    width: 100%;
}

.services-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    list-style: none;
    padding: 0;
}

.services-list li {
    width: calc(50% - 10px);
    margin-bottom: 20px;
    display: flex;
    align-items: center;
}

.service-icon {
    width: 12px; /* Adjust the size of the icon */
    margin-right: 10px;
}

.service-text {
    flex: 1;
    font-size: 13px;
}

.services-container > h1 {
  margin:20px auto ;
  text-align: center;
  padding-top: 20px;
  padding-bottom: 20px;
}